<template>
  <div class="my-focus">
  	<user-center-temp 
  		:active-index='activeIndex'
	  	:item-category="focusCategory"
  		:title="''"
  		@change-active-index="changeActiveIndex">
		<!--   -->
		<div v-show="activeIndex == 0">
			<product-list 
				@show-all-product=""
				:item="''"
				:all-product-list="favoriteList">
			</product-list>
		</div>
		<!--   -->
		<div v-show="activeIndex == 1">
			<product-list
				@show-all-product=""
				:item="''"
				:all-product-list="focusList">
			</product-list>
		</div>
		<!--   -->
		<div v-show="activeIndex == 2">
			<product-list
				:item="''"
				:all-product-list="storeList">
			</product-list>
		</div>
	</user-center-temp>
  </div>
</template>

<script>
import UserCenterTemp from '@/components/UserCenterTemp'
import FocusProductListBase from '@/components/FocusProductListBase'

export default {
	name: 'MyFocus',
	components: {
		'user-center-temp': UserCenterTemp,
		'product-list': FocusProductListBase,
	},
	data () {
		return {
			activeIndex: 0,
			// showAllProduct:
			focusCategory: [
		    	{
					index: 0,
					item: '',
					showNum: 0,
				},
				{
					index: 1,
					item: '',
					showNum: 0,
				},
				{
					index: 2,
					item: '',
					showNum: 0,
				},
			],
			focusList: [
				{
					index: 0,
					imageName: '1-0.jpg',
					name: '   500g  ',
					originalPrice: 10.90,
					currentPrice: 9.90,
					commentNum: 29014,
					favorableRate: 98,
				},
				{
					index: 1,
					imageName: '1-1.jpg',
					name: '  400g   ',
					originalPrice: 29.90,
					currentPrice: 29.90,
					commentNum: 22314,
					favorableRate: 98,
				},
				{
					index: 2,
					imageName: '1-2.jpg',
					name: '  2   ',
					originalPrice: 109.90,
					currentPrice: 108.00,
					commentNum: 8714,
					favorableRate: 98,
				},
				{
					index: 3,
					imageName: '1-3.jpg',
					name: '  24 50-100g',
					originalPrice: 29.90,
					currentPrice: 28.60,
					commentNum: 20914,
					favorableRate: 98,
				},
				{
					index: 4,
					imageName: '1-4.jpg',
					name: ' 1 1.2kg ',
					originalPrice: 48.90,
					currentPrice: 48.90,
					commentNum: 27314,
					favorableRate: 98,
				},
				{
					index: 5,
					imageName: '1-5.jpg',
					name: '   720g    ',
					originalPrice: 35.90,
					currentPrice: 32.90,
					commentNum: 98114,
					favorableRate: 98,
				},
			],
			favoriteList: [
				{
					index: 0,
					imageName: '2-0.jpg',
					name: '  500g5   ',
					originalPrice: 17.90,
					currentPrice: 16.90,
					commentNum: 42314,
					favorableRate: 98,
				},
				{
					index: 1,
					imageName: '2-1.jpg',
					name: '(MSC250g   ',
					originalPrice: 28.90,
					currentPrice: 26.90,
					commentNum: 9084,
					favorableRate: 98,
				},
				{
					index: 2,
					imageName: '2-2.jpg',
					name: '5A   2.5kg',
					originalPrice: 89.90,
					currentPrice: 79.90,
					commentNum: 20314,
					favorableRate: 98,
				},
				{
					index: 3,
					imageName: '2-3.jpg',
					name: ' 41000g',
					originalPrice: 69.90,
					currentPrice: 69.90,
					commentNum: 2624,
					favorableRate: 98,
				},
				{
					index: 4,
					imageName: '2-4.jpg',
					name: '100168 10S ',
					originalPrice: 199.90,
					currentPrice: 188.00,
					commentNum: 7824,
					favorableRate: 98,
				},
				{
					index: 5,
					imageName: '2-5.jpg',
					name: '  200ml*10',
					originalPrice: 49.90,
					currentPrice: 46.90,
					commentNum: 31144,
					favorableRate: 97,
				},
				{
					index: 6,
					imageName: '2-6.jpg',
					name: ' 500g ',
					originalPrice: 44.80,
					currentPrice: 44.80,
					commentNum: 234,
					favorableRate: 99,
				},

			],
			storeList: [],
		}
	},
	methods: {
		// 
		getLowerPriceItems: function (productList) {
			var tempList = [];
			for (var i = 0; i < productList.length; i++) {
				if (productList[i].currentPrice < productList[i].originalPrice) {
					tempList.push(productList[i]);
				}
			}
			return tempList;
		},
		// index
		changeActiveIndex: function (index) {
			this.activeIndex = index;
		},
		// 
		countItemsShowNum: function () {
			this.focusCategory[0].showNum = this.getLowerPriceItems(this.favoriteList).length;
			this.focusCategory[1].showNum = this.getLowerPriceItems(this.focusList).length; 
		},
	},
	mounted () {
		this.countItemsShowNum();
	},
};
</script>

<style scoped>
	.my-focus {
		/*padding: 20px 0 0 20px;*/
	}	
</style>


